import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Button,Modal} from 'antd';
import HangQinV3 from '../../ABI/hangqinV3';
import erc20abi from '../../ABI/ERC20.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class Hangqin extends Component {
      state = {
        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        visible:false,
        tokenadr:'',
        tokenName:'',
        MDEX:'',
        LGDEX:''
      };
      
  async componentDidMount(){
    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']
        
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

    const AllTokensName = await HangQinV3.methods.AllTokensName().call();
    const Allprice = await HangQinV3.methods.Allprice().call();
    const AllTokens = await HangQinV3.methods.AllTokens().call();
    const dataSourceV3 = [];
    for(var i1=0;i1<AllTokensName.length;i1++){
      const price = [];
      price[1] = AllTokensName[i1];
      price[5] = AllTokens[i1];
      var p=0;
        if(Allprice[i1].length < 16){
          p = parseFloat(web3.utils.fromWei(Allprice[i1],"ether")).toExponential(2);
        } else{
          p = parseFloat(web3.utils.fromWei(Allprice[i1],"ether")).toFixed(3);
        }
        if(p>1000){
          p = parseFloat (Allprice[i1]).toFixed(0);
        }
      price[2] = p;
      dataSourceV3.push(price);
      this.setState({message:"加载中……"+ i1 + '=>' + AllTokensName.length});
    }
    this.setState({message:""});
    this.setState({requests:dataSourceV3});

    //设置MDEX链接
    const url =  window.location.host;
    this.setState({MDEX:'https://' + url + '/MDEX'});
    //设置LGDEX链接
    this.setState({LGDEX:'https://' + url + '/LGDEX'});
    
  }

  ShowAssets= async() =>{
    const AllTokens = await HangQinV3.methods.AllTokens().call();
    const data = this.state.requests;
    for(var i2=0; i2<AllTokens.length; i2++ ){
      const ERC20 = await new web3.eth.Contract(erc20abi.abi,AllTokens[i2]);
      const balance =  await ERC20.methods.balanceOf(window.defaultAccount).call(); //原始余额用来计算单价
      const decimals =  await ERC20.methods.decimals().call();
      const __balance = parseFloat(balance*10**(- decimals) ) //减除了18位小数的余额
      //数量科学计数
      var balanceII = '';
      if(__balance > 100000){
        balanceII = __balance.toExponential(1)
      }else{
        balanceII = __balance.toFixed(1)
      }

      //总价值科学计数
      var balanceRMB ;
      balanceRMB = __balance * this.state.requests[i2][2]
      if(balanceRMB < 0.1){
        balanceRMB = balanceRMB.toExponential(1)
      }else{
        balanceRMB = balanceRMB.toFixed(1)
      }
      if(__balance > 0 ){data[i2][3]= balanceII}
      if(balanceRMB > 0){data[i2][4]= balanceRMB}
      this.setState({message:"加载中……"+ i2 + '=>' + AllTokens.length});
      }
      this.setState({message:""});
      this.setState({requests:data});
  }

  handleOk = () => {
    this.setState({visible: false})
  };
    

  render(){
    const content = (
      <div>
        {this.state.tokenName}&emsp;&emsp;
        <CopyToClipboard text={this.state.tokenadr}>
        <Button size='small' >复制合约地址</Button>
        </CopyToClipboard>
      </div>
    );

    const columns = [
      {
        title: '名称',
        dataIndex: '1',
        width: 60,
        align: 'left',
      },
      {
        title: '数量',
        dataIndex: '3',
        width: 60,
        align: 'left',
      },
      
      
      {
        title: '折合￥',
        dataIndex: '4',
        width: 75,
        align: 'left',
      },

      {
        title: '价格(￥)',
        dataIndex: '2',
        align: 'right',
      }
    ];
  return (
    <div>
    <Card  type="inner" size='small' >
      <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
          {this.state.message}
      </div>
      <Table pagination={false} columns={columns}  dataSource={this.state.requests} 
           onRow = {(record) => {
            return {
              onClick: () => {
                this.setState({visible: true})
                this.setState({tokenadr: record[5]})
                this.setState({tokenName: record[1]})
                }
              }
              }
              }         
      /> 
      
    </Card>
    <Card>
        <Button shape='round' type="primary" block color='green' size="middle" 
            style={{marginBottom:'10px'}}
            onClick={() => {this.ShowAssets()}}
            >显示我的资产</Button>
        <Button shape='round' block color='green' size="middle" ><Link to="/Hangqinadd">添加通证(全网可见)</Link></Button>
        <div >注：
          <div>2.8e-7是什么意思？</div>
          <div>科学计数法，2.8e-7=0.00000028</div>
        </div>
        
    </Card>
    <Modal title={content} visible={this.state.visible} onCancel={this.handleOk} 
        footer={[
           ]}
    >
        <div style={{textAlign:'center'}}>
            <div>
              <a href={`${this.state.LGDEX}/#/swap?outputCurrency=${this.state.tokenadr}`}>
                <Button type='primary' block size='large'>前往LGDEX&nbsp;&nbsp;买/卖{this.state.tokenName}</Button>
              </a>
            </div>
            <div style={{marginTop:'10px'}}>
              <a  href={`${this.state.MDEX}/#/swap?outputCurrency=${this.state.tokenadr}`}>
                <Button type='primary' block size='large'>前往MDEX&nbsp;&nbsp;买/卖{this.state.tokenName}</Button>
              </a>
            </div>
        </div>
      </Modal>
      
    </div>
  );
}
}

export default Hangqin;
